import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from "react-router-dom";
import './i18n';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { AppStoreProvider } from './stores/AppStore';
import { createServer } from "miragejs"

if(process.env.USE_MIRAGE) {
  createServer({
    routes() {

      this.get("/api/todos", () => {
        let todos = localStorage.getItem('todos')
        return todos !== null ? JSON.parse(todos) : [
          { id: "1", text: "Open developer tools", complete: false },
          { id: "2", text: "Refresh a page and change counter while todos loading", complete: false },
          { id: "3", text: "Keep smile and manage state easy ;)", complete: false },
        ]
      }, { timing: 5000 })

      this.post("/api/todos", (schema: any, request: {requestBody: string}) => {
        localStorage.setItem('todos', request.requestBody)
        if(false) console.log(schema)
        return {success: true, message: "Saved!"}
      }, { timing: 5000 })

    },
  }) 
}

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <AppStoreProvider>
        <App />
      </AppStoreProvider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

serviceWorkerRegistration.register();
reportWebVitals();
